export default {
  config: {
    inline: {
      type: "radio",
      default: false,
      label: "inline / layout",
      options: [
        { text: "layout", value: false },
        { text: "inline", value: true }
      ]
    },
    isResponsive: {
      type: "switch",
      label: "",
      vif: data => !data.inline,
      options: [
        { text: "", value: true },
        { text: "", value: false }
      ]
    },
    labelPosition: {
      type: "select",
      label: "",
      options(data) {
        const options = ["left", "right", "top"];
        if (data.isResponsive && !data.inline) {
          options.unshift({ text: "", value: null });
        }
        return options;
      }
    },
    span: {
      type: "select",
      label: "",
      vif: data => !data.inline,
      options(data) {
        const options = Array.from({ length: 24 }, (v, i) => {
          return { text: `${24 - i} / 24`, value: 24 - i };
        });
        if (data.isResponsive) {
          options.unshift({ text: "", value: null });
        }
        return options;
      },
      style: {
        width: "100%"
      }
    },
    isDialog: {
      type: "switch",
      label: ""
    },
    isShowLabel: {
      type: "switch",
      label: ""
    },
    labelWidth: {
      type: "input",
      label: "",
      attrs: {
        type: "number",
        min: 0,
        step: 10
      },
      tip: "auto"
    },
    disabled: {
      type: "switch",
      label: ""
    },
    readonly: {
      type: "switch",
      label: ""
    },
    isShowErrorNotify: {
      type: "switch",
      label: ""
    },
    isShowSubmitBtn: {
      type: "radio",
      label: "",
      options: [
        { text: "", value: true },
        { text: "", value: false }
      ]
    },
    isShowResetBtn: {
      type: "radio",
      label: "",
      options: [
        { text: "", value: true },
        { text: "", value: false }
      ]
    },
    isShowBackBtn: {
      type: "radio",
      label: "",
      options: [
        { text: "", value: null },
        { text: "", value: true },
        { text: "", value: false }
      ]
    },
    isShowCancelBtn: {
      type: "radio",
      label: "",
      options: [
        { text: "", value: null },
        { text: "", value: true },
        { text: "", value: false }
      ]
    },
    formBtnSize: {
      type: "select",
      label: "",
      options: [{ text: "", value: null }, "medium", "small", "mini"]
    },
    submitBtnText: {
      type: "input",
      label: ""
    },
    backBtnText: {
      type: "input",
      label: ""
    },
    cancelBtnText: {
      type: "input",
      label: ""
    },
    resetBtnText: {
      type: "input",
      label: ""
    }
  },
  data: {
    inline: false,
    disabled: false,
    readonly: false,
    isShowLabel: true,
    isDialog: false,
    isShowSubmitBtn: true,
    isShowBackBtn: null,
    isShowResetBtn: false,
    isShowCancelBtn: null,
    isResponsive: true,
    isShowErrorNotify: true,
    submitBtnText: "",
    cancelBtnText: "",
    backBtnText: "",
    resetBtnText: "",
    labelWidth: null,
    labelPosition: null,
    span: null,
    formBtnSize: null
  }
};
