import _ from "lodash";

/**
 * 
 * @param {object} obj
 * @example
 * objectToArr({a1: { name: "", age: 10 }, a2: { name: "", age: 20 }}, 'id')
 * // [{ name: "", age: 10, id: 'a1' }, { name: "", age: 20, id: 'a2' } ]
 */
export function objectToArr(obj, keyField) {
  return Object.keys(obj).reduce((acc, key) => {
    acc.push(Object.assign({}, { [keyField]: key }, obj[key]));
    return acc;
  }, []);
}

/**
 * 
 * @param {array} arr
 * @example
 * arrToObject([{ name: "", age: 10, id: 'a1' }, { name: "", age: 20, id: 'a2' } ], 'id')
 * // {a1: { name: "", age: 10 }, a2: { name: "", age: 20 }}
 */
export function arrToObject(arr, key) {
  return _.cloneDeep(arr).reduce((acc, cur) => {
    acc[cur[key]] = cur;
    delete cur[key];

    return acc;
  }, {});
}

//  formDesc  Label
export function changeFormDescLabel(formDesc = {}) {
  return _.mapValues(formDesc, (obj, key) => {
    //  label  key:label
    //  max: { label: "" } => max: { label: "max:" }
    const label = `${key}: ${obj.label}`;

    //  placeholder  label 
    //  placeholder 
    if (!obj.attrs) {
      obj.attrs = {};
    }
    obj.attrs.placeholder = obj.label;
    return { ...obj, label };
  });
}

/**
 * 
 * 1.  2.
 * @param {array} comps 
 * @param {string} type 
 * @param {object} common 
 * @param {object} attrs 
 * comps: [
 *  { type: "input",
 *    config: {
 *      common: { defaultData: {}, requiredData: {} },
 *      attrs: { defaultData: {}, requiredData: {} }
 *    }
 *  }
 * ]
 */
export function addFormItem({ type, field, label, config = {} }) {
  // 
  const formItemConfig = _.cloneDeep(config);
  //  = + + 
  const commonData = {
    ...(formItemConfig?.config?.common?.defaultData || {}),
    ...(formItemConfig?.config?.common?.requiredData || {}),
    // 
    type: type || formItemConfig.type,
    label: label || formItemConfig.label,
    field: field || "key_" + Date.now()
  };

  //  =  + 
  const attrsData = {
    ...(formItemConfig?.config?.attrs?.defaultData || {}),
    ...(formItemConfig?.config?.attrs?.requiredData || {})
  };

  //  + 
  return {
    ...commonData,
    attrs: attrsData
  };
}

/**
 * 
 * @param {string} str
 * @example
 * reomveQuotes({"name": "zhang"}) => {name: "zhang"}
 */
export function reomveQuotes(str) {
  return str.replace(/"(\w+)":/g, "$1:");
}

/**
 * ( +  + )
 */
export function removeUselessAttrs(obj = {}, defaultObj = {}) {
  const isEmpty = val => _.isNil(val);
  const isDefault = (val, key) => _.eq(val, defaultObj[key]);
  const isHide = key => (key || "").startsWith("_");

  return _.omitBy(
    obj,
    (val, key) => isEmpty(val) || isDefault(val, key) || isHide(key)
  );
}
