<template>
  <el-dialog
    :visible="visible"
    append-to-body
    title=""
    :close-on-click-modal="false"
    @update:visible="$emit('change', $event)"
  >
    <el-alert
      title=""
      type="warning"
      show-icon
      style="margin-bottom: 20px"
    ></el-alert>
    <prism-editor
      class="f-render-code"
      v-model="code"
      :highlight="highlighter"
      line-numbers
    ></prism-editor>
    <div style="text-align: center;margin-top: 20px">
      <el-button @click="handleChangeCode" type="primary"></el-button>
      <el-button @click="handleCopyData" type="primary"></el-button>
      <el-button type="primary"
        ><el-link
          :href="fileURL"
          style="color: white;"
          :underline="false"
          :download="`${Date.now()}.js`"
          ></el-link
        ></el-button
      >
      <el-button @click="$emit('change', false)"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import { PrismEditor } from "vue-prism-editor";
import { highlight, languages } from "prismjs/components/prism-core";

import _ from "lodash";
import copy from "clipboard-copy";
import serialize from "serialize-javascript";
import { reomveQuotes } from "../../../utils";

export default {
  inject: ["frender"],
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  components: {
    PrismEditor
  },
  data() {
    return {
      code: ""
    };
  },
  watch: {
    visible(val) {
      if (val) {
        this.code = reomveQuotes(
          serialize(this.frender.changedFormConfig, { space: 2 })
        );
      }
    }
  },
  computed: {
    fileURL() {
      const blob = new Blob([this.code]);
      return URL.createObjectURL(blob);
    }
  },
  methods: {
    // 
    highlighter(code) {
      return highlight(code, languages.js);
    },
    // 
    handleChangeCode() {
      try {
        const res = eval("(" + this.code + ")");
        if (_.isPlainObject(res)) {
          this.frender.updateFormConfig(res);
          this.$emit("change", false);
        } else {
          throw new TypeError(res + ", ");
        }
      } catch (err) {
        this.$message.error(" JS ");
        console.error(err);
      }
    },
    // 
    handleCopyData() {
      copy(this.code);
      this.$message.success("!");
    }
  }
};
</script>
