<template>
  <ele-form-dialog
    v-model="formData"
    :formDesc="batchFormDesc"
    labelPosition="left"
    width="800px"
    :dialogAttrs="{
      'close-on-click-modal': false,
      'append-to-body': true
    }"
    :request-fn="handleAdd"
    :visible="visible"
    @update:visible="$emit('change', $event)"
    title=""
  ></ele-form-dialog>
</template>

<script>
import _ from "lodash";
import { addFormItem } from "../../../utils";

export default {
  inject: ["frender"],
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    // 
    fieldCountObj() {
      return _.countBy(this.frender.formItemList, o => o.field);
    },
    batchFormDesc() {
      return {
        type: {
          type: "radio",
          label: "",
          default: "dynamic",
          options: [
            { text: "", value: "dynamic" },
            {
              text: "()",
              value: "mysql",
              attrs: { disabled: true }
            }
          ]
        },
        formItems: {
          type: "dynamic",
          label: "",
          isTypeChangeReloadValue: false,
          attrs: {
            columns: [
              {
                type: "el-input",
                valueKey: "field",
                attrs: {
                  placeholder: " field"
                }
              },
              {
                type: "el-input",
                valueKey: "label",
                attrs: {
                  placeholder: " label"
                }
              },
              {
                type: "el-select",
                valueKey: "type",
                slots: {
                  default: h => {
                    return this.frender.sortedComps.map(item =>
                      h("el-option", {
                        attrs: {
                          label: `${item.type} - ${item.label}`,
                          value: item.type
                        }
                      })
                    );
                  }
                },
                attrs: {
                  filterable: true,
                  placeholder: " type"
                }
              }
            ]
          },
          rules: {
            type: "array",
            validator: (rule, value, callback) => {
              let errorArr = [];
              if (Array.isArray(value) && value.length) {
                // 
                errorArr = value
                  .map((item, index) =>
                    this.frender.formDesc[item.field] ||
                    this.fieldCountObj[item.field] > 1
                      ? `${index + 1} field `
                      : null
                  )
                  .filter(Boolean);

                // 
                errorArr = errorArr.concat(
                  value
                    .map((item, index) => {
                      const errMsg = [];
                      if (!item.field) errMsg.push("field");
                      if (!item.label) errMsg.push("label");
                      if (!item.type) errMsg.push("type");

                      return errMsg.length
                        ? `${index + 1} ${errMsg.join("")} `
                        : null;
                    })
                    .filter(Boolean)
                );
              }
              errorArr.length
                ? callback(new Error(errorArr.join(" & ")))
                : callback();
            }
          }
        }
      };
    }
  },
  data() {
    return {
      formData: {
        type: "dynamic",
        formItems: []
      }
    };
  },
  methods: {
    handleAdd({ formItems }) {
      try {
        const newFormItems = formItems.map(item =>
          addFormItem({
            ...item,
            config: this.frender.compsMap.get(item.type)
          })
        );

        this.frender.updateFormItemList(
          this.frender.formItemList.concat(newFormItems)
        );

        // 
        this.$message.success("");
        this.formData = {
          type: "dynamic",
          data: []
        };

        this.$emit("change", false);
      } catch (err) {
        console.error(err);
      }
    }
  }
};
</script>
